import React, { useState } from "react";
import { Form, Button, Checkbox, Input, Icon, message } from "antd";
import { useHistory } from "react-router-dom";
import styled from "styled-components";
import { Logo } from "../components";
import { login, register } from "../services/login";

const Wrap = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 5vh;
`;
const Box = styled.div`
  border: 1px solid #d8dee2;
  border-radius: 5px;
  width: 280px;
  height: auto;
  padding: 20px;
  background: #fff;
`;

function Login({ form: { getFieldDecorator, validateFields, getFieldValue } }) {
  const [isRegister, setIsRegister] = useState(false);
  const [confirmDirty, setConfirmDirty] = useState(false);
  const history = useHistory();
  const handleSubmit = e => {
    e.preventDefault();
    validateFields((err, values) => {
      if (!err) {
        if (isRegister) {
          // 
          register(values).then(data => {
            if (data.code === 200) {
              message.success(data.msg);
              setIsRegister(false);
            } else {
              message.info(data && data.msg);
            }
          });
        } else {
          login({
            email: values.email,
            pwd: values.pwd
          }).then(res => {
            if (res.code === 200) {
              sessionStorage.setItem(
                "user",
                JSON.stringify({ email: values.email, ...res.data })
              );
              history.push("/");
            } else {
              message.info(res && res.msg);
            }
          });
        }
      }
    });
  };
  return (
    <Wrap>
      <Logo />
      <h2>Leet Code</h2>
      <Box>
        <Form onSubmit={handleSubmit}>
          <Form.Item>
            {getFieldDecorator("email", {
              rules: [
                { required: true, message: "!" },
                {
                  pattern: /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/,
                  message: "!"
                }
              ]
            })(
              <Input
                prefix={
                  <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />
                }
                placeholder=""
              />
            )}
          </Form.Item>
          <Form.Item>
            {getFieldDecorator("pwd", {
              rules: [
                { required: true, message: "!" },
                {
                  validator(rule, value, callback) {
                    if (value && confirmDirty) {
                      validateFields(["rePwd"], { force: true });
                    }
                    callback();
                  }
                }
              ]
            })(
              <Input
                prefix={
                  <Icon type="lock" style={{ color: "rgba(0,0,0,.25)" }} />
                }
                type="password"
                placeholder=""
              />
            )}
          </Form.Item>
          {isRegister && (
            <Form.Item>
              {getFieldDecorator("rePwd", {
                rules: [
                  { required: true, message: "!" },
                  {
                    validator(rule, value, callback) {
                      if (value && value !== getFieldValue("pwd")) {
                        callback("");
                      }
                      callback();
                    }
                  }
                ]
              })(
                <Input
                  prefix={
                    <Icon
                      type="safety-certificate"
                      style={{ color: "rgba(0,0,0,.25)" }}
                    />
                  }
                  type="password"
                  onBlur={e => {
                    setConfirmDirty(confirmDirty || !!e.target.value);
                  }}
                  placeholder=""
                />
              )}
            </Form.Item>
          )}
          <Form.Item>
            {!isRegister &&
              getFieldDecorator("remember", {
                valuePropName: "checked",
                initialValue: true
              })(<Checkbox></Checkbox>)}
            {!isRegister && (
              <a style={{ float: "right" }} href="">
                
              </a>
            )}
            <Button type="primary" htmlType="submit" style={{ width: "100%" }}>
              {isRegister ? "" : ""}
            </Button>
            {isRegister ? " " : " "}
            <a href="javascript:;" onClick={() => setIsRegister(!isRegister)}>
              {isRegister ? "!" : "!"}
            </a>
          </Form.Item>
        </Form>
      </Box>
    </Wrap>
  );
}
export default Form.create()(Login);
